




















































































































































































































































import {
  defineComponent, ref, onMounted, watch,
} from '@vue/composition-api';

import MaterialTypeCascade from '@/components/MaterialTypeCascade.vue';
import DictionarySelectorNew from '@/components/dictionary/DictionarySelectorNew.vue';
import DictionaryCountrySelector from '@/components/dictionary/DictionaryCountrySelector.vue';
import MaterialColorPicker from '@/views/upload/material/components/ColorPicker.vue';
import { materialForm, materialFromRules } from '@/views/material/form-related.js';
import useClosest from '_/composables/use-closest';
import {
  categoryName2surfaceTechnics, categoryName2MaterialsTechnology, designContent, applicableProductLine,
  MaterialsTechnology, SurfaceTechnics,
} from '@/models/Material';
import { getHost, exec } from '@/utils/exec';
import isQt from '@/utils/is-qt';
import MaterialInfo from '../index.vue';

// 此组件仅用于普通面料编辑、面料库面料上传
export default defineComponent({
  name: 'MaterialForm',
  components: {
    DictionarySelectorNew,
    DictionaryCountrySelector,
    MaterialColorPicker,
    // MaterialTypeCascade,
  },
  props: {
    material: {
      type: Object,
    },
    status: {
      type: String as () => 'uplaod' | 'edit',
      default: 'edit',
    },
    isCreator: {
      type: Boolean,
      default: false,
    },
    entryAppType: {
      type: Number,
    },
    classification: {
      type: String as () => '织物' | '人造革' | '真皮',
      default: '',
    },
  },
  setup(props, { root }) {
    const refsForm = ref(null);
    const newMaterial = ref<Partial<typeof materialForm> & { [key: string]: any }>({});
    if (props.status === 'upload') {
      newMaterial.value = {
        ...materialForm,
        materialsTechnology: [],
        surfaceTechnics: [],
        designContent: [],
        applicableProductLine: [],
      };
    } else {
      newMaterial.value = JSON.parse(JSON.stringify(props.material));
    }

    const shouldUpdateValue = ref<{ [key: string]: any }>({});

    // 材料种类
    const materialsTechnologyList = ref<MaterialsTechnology[]>([]);
    // 表面工艺
    const surfaceTechnicsList = ref<SurfaceTechnics[]>([]);
    if (props.status === 'upload') {
      materialsTechnologyList.value = categoryName2MaterialsTechnology[props.classification as '织物' | '人造革' | '真皮'];
      surfaceTechnicsList.value = categoryName2surfaceTechnics[props.classification as '织物' | '人造革' | '真皮'];
    }
    // 图案内容
    const designContentList = designContent;
    // 适用产品线
    const applicableProductLineList = applicableProductLine;


    // 获取分类信息
    const parentIds = ref<string[]>([]);

    // 类别节点数组
    const categoryArr = ref<any[]>([]);

    watch(() => categoryArr.value, async (v: any[]) => {
      if (props.status === 'edit') {
        materialsTechnologyList.value = categoryName2MaterialsTechnology[v[1].name as '织物' | '人造革' | '真皮'];
        surfaceTechnicsList.value = categoryName2surfaceTechnics[v[1].name as '织物' | '人造革' | '真皮'];
      }
    });

    const materialInfo = useClosest<typeof MaterialInfo>('MaterialInfo');
    // 修改鞋模型信息
    async function updateInfo(prop: string, value: unknown) {
      shouldUpdateValue.value[prop] = value;
      newMaterial.value[prop] = value;
      console.info(materialInfo.value);
      if (materialInfo.value) {
        materialInfo.value.saveBtnDisabled = false;
      }
    }

    async function getFormData() {
      const valid = await refsForm.value!.validate();
      if (!valid) throw new Error('表单校验不通过');
      let formData = {} as { [x: string]: any; };
      if (props.status === 'edit') {
        formData = JSON.parse(JSON.stringify(shouldUpdateValue.value));
      } else {
        formData = JSON.parse(JSON.stringify(newMaterial.value));
      }

      const pickedKeys = ['designContent', 'applicableProductLine', 'surfaceTechnics', 'materialsTechnology'] as const;

      pickedKeys.forEach(k => {
        if (Object.prototype.hasOwnProperty.call(formData, k)) {
          formData[k] = formData[k].join();
        }
      });

      console.info(formData);

      return formData;
    }

    const host = ref(null);

    onMounted(async () => {
      if (props.status === 'upload') {
        host.value = await getHost();

        if (host.value && root.$route.path.includes('/qt/upload/material-library')) {
          host.value!.on('requestSupplierForm', async () => {
            let formData = {};
            try {
              formData = await getFormData();
            } catch (e) {
              formData = {
                success: false,
                errorMsg: '',
                material: null,
              };
            }

            await exec('recvSupplierForm', formData);
          });
        }
      }
    });
    return {
      applicableProductLineList,
      designContentList,
      materialsTechnologyList,
      surfaceTechnicsList,
      rules: materialFromRules,
      // rules: materialLibraryFromRules,
      refsForm,
      updateInfo,
      getFormData,
      newMaterial,
      shouldUpdateValue,
      parentIds,
      categoryArr,
    };
  },
});
