














import i18n from 'i18next';
import { getDictionaryByTypeCode } from '@/api/dictionary';
// import * as DictionaryCache from '@/views/material/cache-dictionary';

type dictionaryUnit = {
  itemValue: number| string,
  label: string,
};

export default {
  name: 'DictionarySelector',
  model: {
    event: 'change',
    prop: 'value',
  },
  props: {
    // 选中的值
    value: {
      type: [String, Number],
      default: 1,
    },
    // 字典类型
    type: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    valueType: {
      type: String as () => 'number'| 'string',
      default: 'number',
    },
  },
  data() {
    return {
      selectedValue: '',
      options: [] as dictionaryUnit[],
    };
  },
  async created() {
    try {
      this.options = (await getDictionaryByTypeCode(
        this.type,
        i18n.language,
      )).data;

      if (this.valueType === 'string') {
        this.options.forEach(o => { o.itemValue = o.itemValue.toString(); });
      }
      // this.options = await DictionaryCache.default.getDictionaryByType(this.type);
    } catch (err) {
      this.$errHandler(err);
    }
  },
  watch: {
    value: {
      handler(val) {
        this.selectedValue = val;
      },
      immediate: true,
    },
  },
  methods: {
    handleChange() {
      const selectedOptions = this.options.find(item => item.value === this.selectedValue);
      this.$emit('change', this.selectedValue, selectedOptions);
    },
  },

};
